.digital {
  border: 10px solid $primary-color;
  border-radius: 10px;
  font-size: 50px;
  padding: 10px 50px;
  min-width: 300px;
  display: flex;
  justify-content: space-between;
  color: $primary-color;

  &__min {
    color: $gold-color;
    width: $window-width;
  }

  &__sec {
    color: $blue-color;
    width: $window-width;
    text-align: right;
  }
}
